:root {
  --font-poppins: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --yellow: hsl(49, 100%, 94%);
  --red: hsl(9, 82%, 57%);
  --blue: hsl(194, 100%, 39%);
  --text: hsl(214, 34%, 27%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  color: var(--text);
  font-family: var(--font-poppins);
  overflow-x: hidden;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

p {
  margin: 0;
}

input {
  color: var(--text);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--text);
}

button,
.button {
  color: var(--white);
  padding: 0.9375rem 0 1.125rem;
}

.container {
  padding: 0 1.25rem;
  max-width: 68.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.overlay {
  width: 100vw;
  height: 100vh;
  position: absolute;
  background: linear-gradient(190deg, #fff9df 33%, white 100%);
}

.promo {
  background: url("../images/background.svg") no-repeat 105% 50%/100vh;
  position: relative;
  padding-top: 4rem;
  overflow: hidden;
  height: 100vh;
  z-index: 2;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5.25rem;
}
.header__logo::before {
  content: "Where promises are kept";
  position: relative;
  left: -3rem;
  display: block;
  height: 3.5625rem;
  width: 17.6875rem;
  text-align: center;
  color: var(--text);
  line-height: 3.25rem;
  background-image: url("../images/tagline.svg");
  margin-bottom: 1.1875rem;
}
.header__promise img {
  margin-bottom: 0.29375rem;
}

.hero {
  display: flex;
  justify-content: space-between;
}
.hero__content .hero__text {
  max-width: 21.9375rem;
  margin-bottom: 3rem;
}
.hero__content .hero__text .hero__suptitle {
  font-size: 1.25rem;
}
.hero__content .hero__text .hero__title {
  font-size: 2.375rem;
  font-weight: 400;
}
.hero__content .hero__form {
  max-width: 28.625rem;
}
.hero__content .hero__form .hero__inputs {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.25rem;
}
.hero__content .hero__form .hero__inputs .hero__input {
  position: relative;
  width: 10.4375rem;
  height: 2.375rem;
}
.hero__content .hero__form .hero__inputs .hero__input input {
  width: 100%;
  background-color: transparent;
  padding-left: 2.125rem;
  border-width: 0;
}
.hero__content .hero__form .hero__inputs .hero__input::before {
  content: "";
  display: block;
  position: absolute;
  left: 0.125rem;
  width: 1.3125rem;
  height: 1.3125rem;
}
.hero__content .hero__form .hero__inputs .hero__input::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.25rem;
  border-radius: 0.125rem;
}
.hero__content .hero__form .hero__inputs .hero__input:nth-child(1) ::placeholder {
  color: var(--red);
}
.hero__content .hero__form .hero__inputs .hero__input:nth-child(1)::before {
  background: url("../images/user.svg") no-repeat 0% 80%;
}
.hero__content .hero__form .hero__inputs .hero__input:nth-child(1)::after {
  background-color: var(--red);
}
.hero__content .hero__form .hero__inputs .hero__input:nth-child(3) ::placeholder {
  color: var(--blue);
}
.hero__content .hero__form .hero__inputs .hero__input:nth-child(3)::before {
  background: url("../images/email.svg") no-repeat 0% 80%;
}
.hero__content .hero__form .hero__inputs .hero__input:nth-child(3)::after {
  background-color: var(--blue);
}
.hero__content .hero__form .hero__textarea {
  width: 28.625rem;
  height: 8.375rem;
  background: url("../images/comment.svg") no-repeat 46% 44%;
  margin-bottom: 2.6875rem;
}
.hero__content .hero__form .hero__textarea textarea {
  position: relative;
  bottom: -0.53125rem;
  width: 26.125rem;
  height: 4.75rem;
  border-width: 0;
  color: var(--text);
  line-height: 1.6;
  font-size: 0.875rem;
  background-color: transparent;
  margin: 1.53125rem 1.25rem;
  overflow: hidden;
  resize: none;
}
.hero__content .hero__form .hero__textarea ::placeholder {
  color: var(--text);
}
.hero__content .hero__form button {
  background: linear-gradient(0deg, #eb5238 0%, #eb6f38 100%);
  padding: 0.875rem 3.5rem 0.875rem 1.5rem;
  border-width: 0;
  border-radius: 1.25rem;
  margin-right: 1.875rem;
  margin-bottom: 3.125rem;
}
.hero__content .hero__form a {
  text-decoration: underline;
}
.hero__warning p {
  position: relative;
  padding-left: 3.25rem;
  max-width: 28.375rem;
}
.hero__warning p::before {
  content: "";
  display: block;
  position: absolute;
  top: 0.5rem;
  left: 0;
  width: 2.125rem;
  height: 2.3125rem;
  background-image: url("../images/shield.svg");
}
.hero img {
  position: relative;
  top: -12.1875rem;
  left: 9.6875rem;
}