@use '../util' as *;

.overlay {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: linear-gradient(190deg, rgba(255,249,223,1) 33%, rgba(255,255,255,1) 100%);
}

.promo {
    background: url('../images/background.svg') no-repeat 105% 50% / 100vh;
    position: relative;
	padding-top: rem(64);
    overflow: hidden;
    height: 100vh;
    z-index: 2;
}